import React, { Component } from 'react'

import { connect } from 'react-redux'
import accountSignOut from '../redux/Actions/account_sign_out'
import accountSignIn from '../redux/Actions/account_sign_in'

import { withRouter } from 'react-router-dom'

//ä¸´æ—¶å†™çš„ä¸€ä¸ªçŠ¶æ€æ£€æŸ¥å·¥å…·
class Checkout extends Component {

  componentDidMount() {
    this.judgeSigninStatus()
  }
  
  //ğŸ‘»çœ‹çœ‹å½“å‰ç™»é™†çŠ¶æ€
  judgeSigninStatus = () => {
    const signInfo = JSON.parse(localStorage.getItem('signInfo'))
    
    if(signInfo === null) {//ğŸ‘¶ç¬¬ä¸€æ¬¡è®¿é—®é¡µé¢ï¼Œæ²¡ç™»é™†è¿‡
      this.props.history.push('/login')
      return
    } else {//ğŸ™â€â™‚ï¸æ›¾ç»ç™»é™†è¿‡ï¼ŒlocalStorageé‡Œé¢æœ‰ç›¸å…³ä¿¡æ¯
      if(signInfo.signin) {//ğŸ™‚å·²ç™»å½•
        this.props.dispatch(accountSignIn)
        return
      } else {//ğŸ™æœªç™»å½•
        this.props.history.push('/')
        return
      }
    }
  }

  //æ³¨é”€ç™»å½•
  handleSignOut = () => {
    //æ”¹reduxçš„state
    this.props.dispatch(accountSignOut)
    //æ”¹localSotrage
    const signInfo = JSON.parse(localStorage.getItem('signInfo'))
    signInfo.signin = false
    localStorage.setItem('signInfo', JSON.stringify(signInfo))
    this.judgeSigninStatus()
  }

  storeState = () => {
    const login = this.props.login
    if(login) {
      return ('å·²ç™»å½•')
    } else {
      return ('æœªç™»å½•')
    }
  }

  localStorageState = () => {
    const login = JSON.parse(localStorage.getItem('signInfo')).signin
    if(login) {
      return ('å·²ç™»å½•')
    } else {
      return ('æœªç™»å½•')
    }
  }

  render() {
    return (
      <div
      style = {{
        border: '1px solid pink',
        display: 'flex',
        justifyContent: 'space-evenly',
        fontSize: '12px'
      }}
      >
        <p>redux storeä¸­çš„çŠ¶æ€ï¼š {this.storeState()}</p>
        <p>localStorage ä¸­çš„çŠ¶æ€ï¼š{this.localStorageState()}</p>
        <button onClick={this.handleSignOut}>æ³¨é”€ç™»å½•</button>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return state
}

//é€šè¿‡connetct() è¿æ¥ç»„ä»¶å’Œreduxæ•°æ®
export default connect(mapStateToProps)(withRouter(Checkout));
