import React, { Component } from 'react'
import { connect } from 'react-redux'
import accountSignIn from '../redux/Actions/account_sign_in'

import { withRouter } from 'react-router-dom'

import NavigationBar from '../components/NavigationBar'

class App extends Component {

  componentDidMount() {
    this.judgeSigninStatus()
  }
  
  //ğŸ‘»çœ‹çœ‹å½“å‰ç™»é™†çŠ¶æ€
  judgeSigninStatus = () => {
    const signInfo = JSON.parse(localStorage.getItem('signInfo'))
    
    if(signInfo === null) {//ğŸ‘¶ç¬¬ä¸€æ¬¡è®¿é—®é¡µé¢ï¼Œæ²¡ç™»é™†è¿‡
      this.props.history.push('/login')
      return
    } else {//ğŸ™â€â™‚ï¸æ›¾ç»ç™»é™†è¿‡ï¼ŒlocalStorageé‡Œé¢æœ‰ç›¸å…³ä¿¡æ¯
      if(signInfo.signin) {//ğŸ™‚å·²ç™»å½•
        this.props.dispatch(accountSignIn)
        return
      } else {//ğŸ™æœªç™»å½•
        this.props.history.push('/')
        return
      }
    }
  }



  render() {
    return (
      <div>
        {this.props.children}
        <NavigationBar />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return state
}

//é€šè¿‡connetct() è¿æ¥ç»„ä»¶å’Œreduxæ•°æ®
export default connect(mapStateToProps)(withRouter(App));
